ude clearfix; }\n.list-unstyled    { @include list-unstyled }\n.no-select        { @include no-select }\n.no-resize        { @include no-resize }\n.hand             { @include hand }\n.fixed            { @include fixed }\n.clip             { @include clip }\n.force-wrap       { @include force-wrap }\n.bordered-section { @include bordered-section }\n.section-divider  { @include section-divider }\n\n/// Sets the specified background color and calculates a dark or light contrasted text color.\n@mixin contrasted($background-color, $dark: $contrasted-dark, $light: $contrasted-light) {\n  color: contrast-color($background-color, $dark, $light);\n\n  &:hover {\n    text-decoration: underline;\n    color: var(--body-text);\n  }\n}\n\n/// Sets base color and darkens bg on hover\n@mixin bg-lighten($bg) {\n  background: $bg;\n  * {\n    background:lighten($bg,20%);\n  }\n}\n\n@mixin link-color($color, $hover) {\n  @if not($hover) {\n    $hover: $color;\n  }\n\n  color: $color;\n\n  &:hover\n   {\n    text-decoration: underline;\n    color: $hover;\n  }\n}\n\n@mixin icon-rotate($degrees, $rotation) {\n  filter: progid:DXImageTransform.Microsoft.BasicImage(rotation=#{$rotation});\n  -webkit-transform: rotate($degrees);\n      -ms-transform: rotate($degrees);\n          transform: rotate($degrees);\n}\n\n@mixin icon-flip($horiz, $vert, $rotation) {\n  filter: progid:DXImageTransform.Microsoft.BasicImage(rotation=#{$rotation});\n  -webkit-transform: scale($horiz, $vert);\n      -ms-transform: scale($horiz, $vert);\n          transform: scale($horiz, $vert);\n}\n\n@mixin input-status-color {\n  &, &.focused {\n    &.success {\n      border: solid 1px var(--success);\n      input, .selected {\n        color: var(--success);\n      }\n\n      .vs__actions:after {\n        color: var(--success);\n      }\n    }\n\n    &.warning {\n      border: solid 1px var(--warning);\n      input, .selected {\n        color: var(--warning);\n      }\n\n      .vs__actions:after {\n        color: var(--warning);\n      }\n    }\n\n    &.error {\n      border: solid 1px var(--error);\n\n      > label {\n        color: var(--error);\n      }\n\n      .vs__actions:after {\n        color: var(--error);\n      }\n    }\n  }\n}\n\n@mixin form-focus {\n  // Focus for form like elements (not to be confused with basic :focus style)\n  outline: none;\n  border-color: var(--outline);\n}\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n$angle: 30deg;\n\n.clock {\n    position: relative;\n    display: inline-block;\n    vertical-align: middle;\n    width: 150px;\n    height: 150px;\n    border: 2px solid;\n    border-radius: 100%;\n    text-align: center;\n    font-size: 14px;\n\n    .hour {\n        position: absolute;\n        top: 0px;\n        left: 50%;\n        display: block;\n        width: 20px;\n        height: 50%;\n        margin-left: -10px;\n        padding-top: 4%;\n        font-weight: 400;\n        transform-origin: bottom;\n        user-select: none;\n        box-sizing: border-box;\n        > span {\n            display: block;\n\n            > i {\n                display: block;\n                font-style: normal;\n            }\n        }\n    }\n\n    @for $i from 2 through 12 {\n        .hour:nth-of-type(#{$i}) {\n            transform: rotatez(#{$angle * ($i - 1)});\n            > span {\n                transform: rotatez(#{-$angle * ($i - 1)});\n            }\n        }\n    }\n\n    .clock-circle {\n        position: absolute;\n        top: 50%;\n        left: 50%;\n        width: 16px;\n        height: 16px;\n        transform: translate(-50%, -50%);\n        border: 2px solid #666666;\n        border-radius: 100%;\n        background-color: #ffffff;\n        z-index: 1;\n        box-sizing: border-box;\n\n        &:before {\n            position: absolute;\n            top: 50%;\n            left: 50%;\n            transform: translate(-50%, -50%);\n            display: block;\n            content: \"\";\n            width: 4px;\n            height: 4px;\n            border-radius: 100%;\n            background-color: #666666;\n        }\n    }\n\n    .clock-hour,\n    .clock-minute,\n    .clock-second {\n        position: absolute;\n        top: 15%;\n        left: 50%;\n        display: block;\n        width: 2px;\n        height: 35%;\n        margin-left: -1px;\n        border-radius: 5px;\n        transform-origin: bottom;\n        background-color: #666666;\n    }\n\n    .clock-hour {\n        top: 30%;\n        width: 4px;\n        height: 20%;\n        margin-left: -2px;\n    }\n\n    .clock-second {\n        width: 1px;\n    }\n}\n\n.clock.is-small {\n    width: 80px;\n    height: 80px;\n    border-width: 1px;\n    font-size: 12px;\n\n    .clock-circle {\n        width: 10px;\n        height: 10px;\n        border-width: 1px;\n        &:before {\n            width: 2px;\n            height: 2px;\n        }\n    }\n}\n"],"sourceRoot":""}]);
